import React, { useState, useEffect } from "react";
import "./PortablesPage.css";

import * as api from "../../api/BackendApi";
import { Input } from "reactstrap";

import { Spinner, Jumbotron, Header } from "../Shared";
import Customer from "./Customer";
import Note from "./Note";

function PortablesPage() {
  const [customers, setCustomers] = useState([]);
  const [filteredCustomers, setFilteredCustomers] = useState([]);

  const [activeCustomer, setActiveCustomer] = useState(null);
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    fetchData();
  }, []);

  function fetchData() {
    var params = {
      database: "portables",
      collection: "customers",
    };

    api.dbRead(params).then(setInitialState);
  }

  function setInitialState(customers) {
    setActiveCustomer(customers[0]);
    setCustomers(customers);
    setFilteredCustomers(customers);
  }

  function onCustomerClick(customer) {
    setSearchQuery("");
    setActiveCustomer(customer);
  }

  function onSearchChange(event) {
    const { value } = event.target;

    setSearchQuery(value);

    var keys = [
      "name",
      "product",
      "freshdesk_tickets",
      "purchase_invoices",
      "repair_invoices",
      "serial_numbers",
      "fault",
    ];

    setFilteredCustomers(filterArray(customers, keys, value));
  }

  function filterArray(array, keys, query) {
    return array.filter((element) => {
      return keys
        .map((key) => {
          switch (typeof element[key]) {
            case "string":
              return element[key].search(new RegExp(query, "i")) > -1;
            case "object":
              return element[key]
                .map((child) => child.search(new RegExp(query, "i")) > -1)
                .includes(true);
            default:
              return false;
          }
        })
        .includes(true);
    });
  }

  return (
    <div className="container">
      <Jumbotron>Portables</Jumbotron>
      {customers.length === 0 ? (
        <Spinner style={{ marginTop: "120px" }} />
      ) : (
        <div className="window">
          <div className="left-window">
            <Header dark style={{ padding: "5px" }}>
              Customers
            </Header>
            <Input
              type="text"
              placeholder="Search"
              value={searchQuery}
              onChange={onSearchChange}
            />
            {filteredCustomers.map((customer, index) => (
              <Customer
                key={index}
                customer={customer}
                isActiveCustomer={customer === activeCustomer}
                onClick={onCustomerClick}
              />
            ))}
          </div>
          <div className="right-window">
            <Header dark style={{ padding: "5px" }}>
              {activeCustomer.name}
            </Header>
            <div className="notes-container">
              {activeCustomer.notes.map((note, index) => (
                <Note key={index} note={note} />
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default PortablesPage;
